.login-wrapper{
    display:flex;
    justify-content: center;
}

.login-card{
    width:27vw;
    margin-top: 8%;
}

.insta-logo{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3%;
}

.insta-logo img{
    width: 15vw;
}

.imgcar{
height: 590px;
width:441px;
margin-top: 8vh;
position: relative;
}

.car{
    position:absolute;
    
    width:250px;
    height:520px;
    top:20px;
    left:143px;
}

.car > .carousel{
    height:100%;
}

@media screen and (max-width:620px) {
    .login-card{
        width: 60%;
       
    }
    .insta-logo{
        width: 35%;
    }
    .imgcar{
        display: none;
    }
    .card-bot p{
        display: none;
    }
}