.signup-wrapper{
    display:flex;
    justify-content: center;
}

.signup-card{
    width:27vw;
    margin-top: 5%;
}

.insta-logo{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3%;
}

.insta-logo img{
    width: 15vw;
}

@media screen and (max-width:620px) {
    .signup-card{
        width: 60%;
       
    }
    .insta-logo{
        width: 35%;
    }
}