.video-container{
    height:80vh;
    border-radius: 20px;
    background-color: white;
    overflow: scroll;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
}

.video-container::-webkit-scrollbar{
    display: none;;
}

.videos{
    position:relative;
}
.fa{
    position:absolute;
    bottom:8%;
    left:10%;
}

.fa h4{
    color:white;
    margin-top: 8%;
    margin-left: 15%;
}